import React, { useState, useEffect } from "react";
import { useDispatch } from "react-redux";
import { Container, Row, Col } from "react-bootstrap";
import SecIntro from "./SecIntro";
import PageTopBanner from "./PageTopBanner";
import {
  startLoading,
  finishLoading,
} from "../redux/features/loader/loaderSlice";
import { apiPost } from "../utilities/axiosInstance";
import API_ENDPOINTS from "../utilities/apiEndPoints";
import WhyChooseUsIco from "../assets/images/whyChooseUs.svg";

const introData = {
  titleStart: "Who is ",
  highlight: "Licence Now?",
  titleEnd: "",
  theme: "",
};

function AboutUs() {
  const [aboutUsPageData, setAboutUsPageData] = useState(null);
  const [error, setError] = useState(null); // State to hold error messages
  const dispatch = useDispatch();

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    try {
      dispatch(startLoading());

      const aboutUsResponse = await apiPost(API_ENDPOINTS.ABOUT, {
        page: "about",
      });

      if (!aboutUsResponse || !aboutUsResponse.data) {
        throw new Error("Invalid response format for About Us data");
      }

      setAboutUsPageData(aboutUsResponse.data);
      setError(null); // Clear any previous errors
    } catch (error) {
      setError(error.message); // Set the error message to state
      console.error(error); // Log error for debugging
    } finally {
      dispatch(finishLoading());
    }
  };

  if (error) {
    return <div className="alert alert-danger">{error}</div>;
  }

  return (
    <>
      <PageTopBanner title="About Us" />
      <div className="WhyChooseUs bg-white">
        <Container>
          <Row>
            <Col lg={12}>
              <SecIntro
                titleStart={introData.titleStart}
                highlight={introData.highlight}
                titleEnd={introData.titleEnd}
                theme={introData.theme}
              />
              <div className="WhyChooseUs__wrp">
                <div className="WhyChooseUs__left">
                  <h4>{aboutUsPageData?.title || "Loading..."}</h4>
                  <p
                    dangerouslySetInnerHTML={{
                      __html:
                        aboutUsPageData?.description ||
                        "Loading description...",
                    }}
                  ></p>
                </div>
                <div className="WhyChooseUs__right">
                  <img src={WhyChooseUsIco} alt="Why Choose Us" />
                </div>
              </div>
            </Col>
          </Row>
        </Container>
      </div>
    </>
  );
}

export default AboutUs;
